:root {
    font-size: 60%;
  
    --color-background: #F0F0F7;
    --color-primary-lighter: #2515ff;
    --color-primary-light: #0c00b9;
    --color-primary: #fffce9;
    --color-primary-dark: #774DD6;
    --color-primary-darker: #6842C2;
    --color-secundary: #f56e00;
    --color-secundary-dark: #c05600;
    --color-title-in-primary: #070707;
    --color-text-in-primary: #070707;
    --color-text-title: #32264D;
    --color-text-complement: #9C98A6;
    --color-text-base: #6A6180;
    --color-line-in-white: #E6E6F0;
    --color-input-background: #F8F8FC;
    --color-button-text: #FFFFFF;
    --color-box-base: #fffce9;
    --color-box-footer: #FAFAFC;
    --color-car-reserve-box: #FAFAFC;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
  
html, body, #root {
    height: 100vh;
}
  
body {
    background: var(--color-background);
}
  
#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

body,
input,
button,
textarea {
  font: 500 1.6rem Poppins;
  color: var(--color-text-base);
}

.container {
    width: 90vw;
    max-width: 700px;
}

@media (min-width: 700px) {
    :root {
      font-size: 62.5%;
    }
}